import React, { useState, useEffect } from 'react';
import { useNavigate, Link, useSearchParams } from 'react-router-dom';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Eye, EyeOff, Mail, Lock, User, ArrowRight, Wallet, UserPlus, Phone, Store, Share2 } from 'lucide-react';
import { useToast } from '@/hooks/use-toast';
import { useFirebaseAuth } from '@/contexts/FirebaseAuthContext';

const UserRegisterPage = () => {
  const [searchParams] = useSearchParams();
  const referralCode = searchParams.get('ref');
  
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    password: '',
    confirmPassword: '',
    phone: '',
    shopName: ''
  });
  const [showPassword, setShowPassword] = useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [referralInfo, setReferralInfo] = useState<string | null>(null);
  
  const { signUp } = useFirebaseAuth();
  const { toast } = useToast();
  const navigate = useNavigate();

  useEffect(() => {
    if (referralCode) {
      setReferralInfo(`تم استخدام رمز الإحالة: ${referralCode}`);
      toast({
        title: "رمز الإحالة مُطبق",
        description: `تم تطبيق رمز الإحالة: ${referralCode}`,
      });
    }
  }, [referralCode, toast]);

  const handleInputChange = (field: string, value: string) => {
    setFormData(prev => ({
      ...prev,
      [field]: value
    }));
  };

  const validateForm = () => {
    if (!formData.name || !formData.email || !formData.password || !formData.confirmPassword || !formData.phone || !formData.shopName) {
      setError('يرجى ملء جميع الحقول');
      return false;
    }

    if (formData.name.length < 2) {
      setError('يجب أن يكون الاسم أكثر من حرفين');
      return false;
    }

    if (formData.shopName.length < 2) {
      setError('يجب أن يكون اسم المحل أكثر من حرفين');
      return false;
    }

    const digits = formData.phone.replace(/\D/g, '');
    const egyptPhoneRegex = /^01(0|1|2|5)\d{8}$/;
    if (!egyptPhoneRegex.test(digits)) {
      setError('رقم مصري غير صالح: 11 رقم ويبدأ بـ 010/011/012/015');
      return false;
    }

    if (formData.password.length < 6) {
      setError('يجب أن تكون كلمة المرور 6 أحرف على الأقل');
      return false;
    }

    if (formData.password !== formData.confirmPassword) {
      setError('كلمات المرور غير متطابقة');
      return false;
    }

    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    if (!emailRegex.test(formData.email)) {
      setError('البريد الإلكتروني غير صالح');
      return false;
    }

    return true;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');
    
    if (!validateForm()) {
      return;
    }

    setLoading(true);
    
    try {
      // تطبيع البريد وكلمة المرور قبل الإنشاء
      const normalizedEmail = (formData.email || '').trim().toLowerCase();
      const normalizedPassword = (formData.password || '').trim();
      await signUp(normalizedEmail, normalizedPassword, formData.name, formData.phone, formData.shopName, referralCode || undefined);
      toast({
        title: "تم إنشاء الحساب بنجاح",
        description: referralCode ? "تم إنشاء الحساب بنجاح مع رمز الإحالة" : "سيتم مراجعة حسابك من قبل الإدارة قبل تفعيله",
      });
      // سيتم التوجيه التلقائي عبر Index.tsx بناءً على حالة الموافقة
      navigate('/dashboard-redirect');
    } catch (error: any) {
      console.error('Registration error:', error);
      let errorMessage = 'حدث خطأ أثناء إنشاء الحساب';
      
      if (error.code === 'auth/email-already-in-use') {
        errorMessage = 'البريد الإلكتروني مستخدم بالفعل';
      } else if (error.code === 'auth/invalid-email') {
        errorMessage = 'البريد الإلكتروني غير صالح';
      } else if (error.code === 'auth/weak-password') {
        errorMessage = 'كلمة المرور ضعيفة جداً';
      } else if (error.code === 'auth/operation-not-allowed') {
        errorMessage = 'إنشاء الحسابات غير مفعل حالياً';
      }
      
      setError(errorMessage);
      toast({
        title: "خطأ في إنشاء الحساب",
        description: errorMessage,
        variant: "destructive",
      });
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-background flex items-center justify-center p-4">
      <div className="w-full max-w-md">
        {/* Logo and Title */}
        <div className="text-center mb-8 fade-in-up">
          <div className="inline-flex items-center justify-center w-16 h-16 bg-gradient-to-br from-green-500 to-emerald-600 rounded-full mb-4 shadow-lg">
            <Wallet className="h-8 w-8 text-white" />
          </div>
          <h1 className="text-3xl font-bold text-gray-900 mb-2">كاشي لينك</h1>
          <p className="text-gray-600">نظام إدارة المحافظ الإلكترونية</p>
        </div>

        {/* Registration Form */}
        <Card className="shadow-xl border-0 bg-white/80 backdrop-blur-sm card-glow fade-in-up">
          <CardHeader className="text-center pb-4">
            <CardTitle className="text-2xl font-bold text-gray-900 flex items-center justify-center gap-2">
              <UserPlus className="h-6 w-6 text-green-600" />
              إنشاء حساب جديد
            </CardTitle>
            <p className="text-gray-600 text-sm mt-2">
              أدخل بياناتك لإنشاء حساب جديد
            </p>
          </CardHeader>
          
          <CardContent className="space-y-6">
            {referralInfo && (
              <Alert className="fade-in-up border-green-200 bg-green-50">
                <Share2 className="h-4 w-4 text-green-600" />
                <AlertDescription className="text-green-700">
                  {referralInfo}
                </AlertDescription>
              </Alert>
            )}

            {error && (
              <Alert variant="destructive" className="fade-in-up">
                <AlertDescription>{error}</AlertDescription>
              </Alert>
            )}

            <form onSubmit={handleSubmit} className="space-y-4">
              {/* Name Field */}
              <div className="space-y-2 fade-in-up">
                <Label htmlFor="name" className="text-sm font-medium text-gray-700">
                  الاسم الكامل
                </Label>
                <div className="relative">
                  <User className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
                  <Input
                    id="name"
                    type="text"
                    value={formData.name}
                    onChange={(e) => handleInputChange('name', e.target.value)}
                    placeholder="أدخل اسمك الكامل"
                    className="pl-10 h-12 text-sm transition-all duration-300 hover:border-green-400 focus:border-green-500 bg-white/50"
                    disabled={loading}
                  />
                </div>
              </div>

              {/* Phone Field */}
              <div className="space-y-2 fade-in-up">
                <Label htmlFor="phone" className="text-sm font-medium text-gray-700">
                  رقم الجوال
                </Label>
                <div className="relative">
                  <Phone className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
                  <Input
                    id="phone"
                    type="tel"
                    value={formData.phone}
                    onChange={(e) => handleInputChange('phone', e.target.value)}
                    placeholder="أدخل رقم مصري (11 رقم يبدأ بـ 010/011/012/015)"
                    dir="ltr"
                    inputMode="numeric"
                    className="pl-10 h-12 text-sm transition-all duration-300 hover:border-green-400 focus:border-green-500 bg-white/50"
                    disabled={loading}
                  />
                </div>
              </div>

              {/* Shop Name Field */}
              <div className="space-y-2 fade-in-up">
                <Label htmlFor="shopName" className="text-sm font-medium text-gray-700">
                  اسم المحل
                </Label>
                <div className="relative">
                  <Store className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
                  <Input
                    id="shopName"
                    type="text"
                    value={formData.shopName}
                    onChange={(e) => handleInputChange('shopName', e.target.value)}
                    placeholder="أدخل اسم المحل الخاص بك"
                    className="pl-10 h-12 text-sm transition-all duration-300 hover:border-green-400 focus:border-green-500 bg-white/50"
                    disabled={loading}
                  />
                </div>
              </div>

              {/* Email Field */}
              <div className="space-y-2 fade-in-up">
                <Label htmlFor="email" className="text-sm font-medium text-gray-700">
                  البريد الإلكتروني
                </Label>
                <div className="relative">
                  <Mail className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
                  <Input
                    id="email"
                    type="email"
                    value={formData.email}
                    onChange={(e) => handleInputChange('email', e.target.value)}
                    placeholder="أدخل بريدك الإلكتروني"
                    className="pl-10 h-12 text-sm transition-all duration-300 hover:border-green-400 focus:border-green-500 bg-white/50"
                    disabled={loading}
                  />
                </div>
              </div>

              {/* Password Field */}
              <div className="space-y-2 fade-in-up">
                <Label htmlFor="password" className="text-sm font-medium text-gray-700">
                  كلمة المرور
                </Label>
                <div className="relative">
                  <Lock className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
                  <Input
                    id="password"
                    type={showPassword ? 'text' : 'password'}
                    value={formData.password}
                    onChange={(e) => handleInputChange('password', e.target.value)}
                    placeholder="أدخل كلمة المرور (6 أحرف على الأقل)"
                    className="pl-10 pr-10 h-12 text-sm transition-all duration-300 hover:border-green-400 focus:border-green-500 bg-white/50"
                    autoComplete="new-password"
                    disabled={loading}
                  />
                  <button
                    type="button"
                    onClick={() => setShowPassword(!showPassword)}
                    className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400 hover:text-gray-600 transition-colors"
                    disabled={loading}
                  >
                    {showPassword ? (
                      <EyeOff className="h-4 w-4" />
                    ) : (
                      <Eye className="h-4 w-4" />
                    )}
                  </button>
                </div>
              </div>

              {/* Confirm Password Field */}
              <div className="space-y-2 fade-in-up">
                <Label htmlFor="confirmPassword" className="text-sm font-medium text-gray-700">
                  تأكيد كلمة المرور
                </Label>
                <div className="relative">
                  <Lock className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
                  <Input
                    id="confirmPassword"
                    type={showConfirmPassword ? 'text' : 'password'}
                    value={formData.confirmPassword}
                    onChange={(e) => handleInputChange('confirmPassword', e.target.value)}
                    placeholder="أعد إدخال كلمة المرور"
                    className="pl-10 pr-10 h-12 text-sm transition-all duration-300 hover:border-green-400 focus:border-green-500 bg-white/50"
                    autoComplete="new-password"
                    disabled={loading}
                  />
                  <button
                    type="button"
                    onClick={() => setShowConfirmPassword(!showConfirmPassword)}
                    className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400 hover:text-gray-600 transition-colors"
                    disabled={loading}
                  >
                    {showConfirmPassword ? (
                      <EyeOff className="h-4 w-4" />
                    ) : (
                      <Eye className="h-4 w-4" />
                    )}
                  </button>
                </div>
              </div>

              {/* Register Button */}
              <Button
                type="submit"
                disabled={loading || !formData.name || !formData.email || !formData.password || !formData.confirmPassword || !formData.phone || !formData.shopName}
                className="w-full h-12 bg-gradient-to-r from-green-500 to-emerald-600 hover:from-green-600 hover:to-emerald-700 text-white font-medium transition-all duration-300 btn-bounce hover-glow fade-in-up"
              >
                {loading ? (
                  <div className="flex items-center gap-2">
                    <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                    جاري إنشاء الحساب...
                  </div>
                ) : (
                  <div className="flex items-center gap-2">
                    إنشاء حساب جديد
                    <ArrowRight className="h-4 w-4" />
                  </div>
                )}
              </Button>
            </form>

            {/* Login Link */}
            <div className="text-center pt-4 border-t border-gray-200 fade-in-up">
              <p className="text-sm text-gray-600">
                لديك حساب بالفعل؟{' '}
                <Link
                  to="/user/login"
                  className="text-green-600 hover:text-green-700 font-medium transition-colors hover:underline"
                >
                  تسجيل الدخول
                </Link>
              </p>
            </div>


          </CardContent>
        </Card>

        {/* Footer */}
        <div className="text-center mt-8 fade-in-up">
          <p className="text-xs text-gray-500">
            © 2024 كاشي لينك. جميع الحقوق محفوظة.
          </p>
        </div>
      </div>
    </div>
  );
};

export default UserRegisterPage;